:root {
  --accent: #3b82f6;
  --border-light: #e5e7eb;
  --bg-gray-50: #f9fafb;
}

.slot-list-container {
  padding: 2rem;
  background: var(--bg-gray-50);
  border-radius: 12px;
  box-shadow: var(--shadow-md, 0 2px 8px rgba(0,0,0,0.07));
  max-width: 480px;
  margin: 0 auto 2rem auto;
}
.slot-filter-bar {
  margin-bottom: 1.5rem;
}
.slot-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.slot-list > li {
  background: white;
  border: 1px solid var(--border-light);
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: box-shadow 0.2s;
  gap: 1rem;
}
.slot-list > li.selected {
  border: 2px solid var(--accent);
  box-shadow: 0 2px 12px rgba(59, 130, 246, 0.04);
}
.btn-primary {
  background: var(--accent);
  color: white;
  border: 1px solid var(--accent);
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-primary:hover {
  background: #2563eb;
}
.error {
  color: #dc2626;
  margin: 1rem 0;
}
.empty-state {
  text-align: center;
  color: #636363;
  font-style: italic;
  padding: 1rem 0;
}
