:root {
  --accent: #3b82f6;
  --border-light: #e5e7eb;
  --bg-white: #fff;
}
.form-container {
  max-width: 440px;
  margin: 0 auto 1.5rem auto;
  padding: 2rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.09);
  border-radius: 12px;
  background: var(--bg-white);
}
.form-container h3 {
  margin-bottom: 1.5rem;
}
label {
  display: block;
  font-size: 0.97rem;
  font-weight: 500;
  margin-bottom: 0.2rem;
}
input[type="datetime-local"],
input[type="text"],
input[type="number"],
input[type="password"],
input[type="email"],
input[type="url"] {
  width: 100%;
  padding: 0.45rem;
  border: 1px solid var(--border-light);
  border-radius: 6px;
  margin-bottom: 1.2rem;
  font-size: 1rem;
  transition: border-color 0.2s;
}
input:focus {
  border-color: var(--accent);
  outline: none;
}
.btn-primary {
  background: var(--accent);
  color: white;
  border: 1px solid var(--accent);
  padding: 0.5rem 1.4rem;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.btn-primary[disabled] {
  opacity: 0.65;
  pointer-events: none;
}
.btn-primary:hover {
  background: #2563eb;
}
.error {
  color: #dc2626;
  margin: 0.7rem 0;
}
