/* Global styling */

:root {
  --accent: #3b82f6;
  --bg-gray-50: #f9fafb;
  --bg-white: #fff;
}

body {
  background: var(--bg-gray-50);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  color: #1f2937;
  margin: 0;
}

.app-main {
  padding: 2.5rem 0;
  min-height: 100vh;
}

h1 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--accent);
}

@media (max-width: 600px) {
  .app-main {
    padding: 1.2rem 0.2rem;
  }
  .form-container, .user-bookings-panel, .slot-list-container {
    padding: 1rem;
  }
}
