.user-bookings-panel {
  max-width: 670px;
  margin: 2rem auto;
  background: #fff;
  padding: 2rem 2.2rem;
  border-radius: 14px;
  box-shadow: var(--shadow-md, 0 2px 8px rgba(0,0,0,0.09));
}
.ub-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.ub-row {
  border: 1px solid #e1e1e1;
  border-radius: 10px;
  background: #f9fafb;
  margin: 1rem 0;
  padding: 1.2rem;
  display: flex;
  gap: 2rem;
  align-items: center;
}
.ub-slot-num {
  font-size: 1.2rem;
  font-weight: 600;
  background: #e6f4ff;
  padding: 0.1rem 0.55rem;
  border-radius: 5px;
}
.ub-status {
  font-size: 1rem;
  font-weight: 600;
  text-transform: capitalize;
}
.status-confirmed {
  color: #4caf50;
}
.status-cancelled {
  color: #f44336;
}
.btn-secondary {
  background: transparent;
  color: #3b82f6;
  border: 1px solid #3b82f6;
  padding: 0.45rem 1.1rem;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.btn-secondary:hover {
  background: #3b82f6;
  color: #fff;
}
.error {
  color: #dc2626;
  margin: 1rem 0;
}
.empty-state {
  text-align: center;
  color: #636363;
  font-style: italic;
  padding: 1rem 0;
}
